// Font styles
$sans: "Fira Sans",
-apple-system,
blinkmacsystemfont,
roboto,
"Segoe UI",
oxygen,
ubuntu,
cantarell,
"Open Sans",
"Helvetica Neue",
sans-serif;

$serif: "DM Serif Text",
georgia,
times,
serif;

// Breakpoints 
$tablet: 640px;
$laptop: 1024px;
$desktop: 1280px;

// Sizes
$base: 180px;
$navbar: 3rem;

.index {
    margin-top: $navbar;
}

// Box Shadow 
$box-shadow: 3px 3px 4px rgba(0, 0, 0, .12),
-3px 2px 4px rgba(0, 0, 0, .12),
5px 10px 15px rgba(0, 0, 0, .14),
-5px 5px 15px rgba(0, 0, 0, .14);

$box-shadow: 0 8px 17px 2px rgba(0, 0, 0, .14),
0 3px 14px 2px rgba(0, 0, 0, .12),
0 5px 5px -3px rgba(0, 0, 0, .2);

@mixin transition($property) {
    transition: $property .2s cubic-bezier(.41, .56, .05, .95);
}