.author-meta {
  justify-content: flex-start;
  transition: all 0.2s cubic-bezier(0.41, 0.56, 0.05, 0.95);
}

.author-meta a {
  margin-right: .5em;
}

.author-meta a:last-child {
  margin-right: 0;
}

.author-meta svg {
  width: 1.5em;
  height: 1.5em;
}

.author-small-header {
  margin-top: 1em;
  font-size: .8em;
  font-weight: 600;
  color: #8b8b92;
  text-transform: uppercase;
}

.author-location p {
  margin-left: .25em;
}

.website svg,
.author-location svg {
  width: 1.5em;
  height: 1.5em;
  fill: none;
  stroke: currentColor;
  transition: stroke 0.2s cubic-bezier(0.41, 0.56, 0.05, 0.95);
}

.website:hover svg {
  stroke: #c70d3a;
}
